import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class UsuariosFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private usuario$ = new BehaviorSubject<any[]>([]);
  //Este va en el HTML para acceder desde otro componente
  public responseUsuarios$: Observable<any[]> = this.usuario$.asObservable();

  private TipoUsuario$ = new BehaviorSubject<any[]>([]);
  public responseTipoUsuarios$: Observable<any[]> = this.TipoUsuario$.asObservable();

  private persona$ = new BehaviorSubject<any[]>([]);
  public responsePersonas$: Observable<any[]> = this.persona$.asObservable();


  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarUsuario(params: any) {
    this.Cargando$.next(true);
    this.usuario$.next([]);
    const request$ = this.dataApi.GetDataApi(`seguridad/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        
        this.Cargando$.next(false);
        this.usuario$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.usuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los  usuarios', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarUsuario(params: any, respuesta) {
    this.Cargando$.next(true);
    this.usuario$.next([]);
    const request$ = this.dataApi.PostDataApi(`seguridad/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.usuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarUsuario(params: any, respuesta) {
    this.Cargando$.next(true);
    this.usuario$.next([]);
    const request$ = this.dataApi.PutDataApi(`seguridad/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.usuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarUsuario(params: any, respuesta) {
    this.Cargando$.next(true);
    this.usuario$.next([]);
    const request$ = this.dataApi.DeleteDataApiUrl(`seguridad/usuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.usuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el usuario', '');
        return EMPTY;
      })
    );
    

    return request$.subscribe();
  }

  MostrarTipoUsuarios(params: any) {
    this.Cargando$.next(true);
    this.TipoUsuario$.next([]);
    const request$ = this.dataApi.GetDataApi(`seguridad/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        
        this.Cargando$.next(false);
        this.TipoUsuario$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los tipos de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarPersona(params: any) {
    this.Cargando$.next(true);
    this.persona$.next([]);
    const request$ = this.dataApi.GetDataApi(`personas/personas/`, params).pipe(
      tap((result: DataApi | any) => {
        
        this.Cargando$.next(false);
        this.persona$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.persona$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar las personas', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
