import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class MenusFacadeService {

  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private Menus$ = new BehaviorSubject<any[]>([]);
  public responseMenus$: Observable<any[]> = this.Menus$.asObservable();


  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarMenus(params: any) {
    this.Cargando$.next(true);
    this.Menus$.next([]);
    const request$ = this.dataApi.GetDataApi(`seguridad/menu/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.Menus$.next(result.data);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.Menus$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los menus', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarMenusAsignacion(params: any, callback) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.GetDataApi(`seguridad/menu/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        callback(result.data)
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los menus', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarMenus(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PostDataApi(`seguridad/menu/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el menu', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarMenus(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.PutDataApi(`seguridad/menu/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el menu', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarMenus(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.DeleteDataApiUrl(`seguridad/menu/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el menu', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }
}
