import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, EMPTY, Observable } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DataApi } from 'src/app/Model/dataApi';
import { DataApiService } from 'src/app/services/data-api.service';
import { MensajesHttpService } from 'src/app/utils/mensajesHttp/mensajes-http.service';

@Injectable({
  providedIn: 'root'
})
export class TipoUsuarioFacadeService {
  private Cargando$ = new BehaviorSubject<boolean>(false);
  public responseCargando$: Observable<boolean> = this.Cargando$.asObservable();

  private TipoUsuario$ = new BehaviorSubject<any[]>([]);
  public responseTipoUsuarios$: Observable<any[]> = this.TipoUsuario$.asObservable();

  private RelacionTipoUsuarioMenu$ = new BehaviorSubject<any[]>([]);
  public responseRelacionTipoUsuarioMenu$: Observable<any[]> = this.RelacionTipoUsuarioMenu$.asObservable();

  constructor(public dataApi:DataApiService, public _mensajesHttp:MensajesHttpService) { }

  
  MostrarTipoUsuarios(params: any) {
    this.Cargando$.next(true);
    this.TipoUsuario$.next([]);
    const request$ = this.dataApi.GetDataApi(`seguridad/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next(result.data.Table0);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los tipos de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  InsertarTipoUsuarios(params: any, respuesta) {
    this.Cargando$.next(true);
    this.TipoUsuario$.next([]);
    const request$ = this.dataApi.PostDataApi(`seguridad/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al insertar el tipo de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  ActualizarTipoUsuarios(params: any, respuesta) {
    this.Cargando$.next(true);
    this.TipoUsuario$.next([]);
    const request$ = this.dataApi.PutDataApi(`seguridad/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al actualizar el tipo de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  EliminarTipoUsuarios(params: any, respuesta) {
    this.Cargando$.next(true);
    this.TipoUsuario$.next([]);
    const request$ = this.dataApi.DeleteDataApiUrl(`seguridad/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this.TipoUsuario$.next([]);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al eliminar el tipo de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  MostrarRelacionTipoUsuarioMenu(params: any, callback) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.GetDataApi(`seguridad/relacionTipoUsr/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        this.Cargando$.next(false);
        callback(result);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al mostrar los menus del tipo usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  insertarRelacionTipoUsuarioMenu(params: any, respuesta) {
    const request$ = this.dataApi.PostDataApi(`seguridad/relacionTipoUsr/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
      
      }),
      catchError((error: HttpErrorResponse) => {
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al asignar el menu al tipo de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

  deleteRelacionTipoUsuarioMenu(params: any, respuesta) {
    this.Cargando$.next(true);
    const request$ = this.dataApi.DeleteDataApiUrl(`seguridad/relacionTipoUsr/tipoUsuario/`, params).pipe(
      tap((result: DataApi | any) => {
        respuesta(result);
        this.Cargando$.next(false);
      }),
      catchError((error: HttpErrorResponse) => {
        this.Cargando$.next(false);
        this._mensajesHttp.mostrarErrorHttp(error, 'Ocurrio un error al remover el menu al tipo de usuario', '');
        return EMPTY;
      })
    );

    return request$.subscribe();
  }

}
